.news-list {
	display: grid;
	gap: 1.5rem;
	&__item {
		display: grid;
	}
	&__date {
		color: var(--front-primary_75p);
		@include style_1\2_1\25(400);
	}
	&__link {
		color: var(--front-brand);
		@include style_1\5_1\33;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}
