//        - registration,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.registration {
	&__descr {
		color: var(--front-primary_50p);
		margin-bottom: 3rem;
	}
	.form__submit {
		width: 100%;
		@media screen and (min-width: $screen-t) {
			width: 25rem;
		}
	}
	:is(&) .section__block + .section__block_last {
		margin-top: 4rem;
	}
	.buttons-set {
		gap: 2rem;
	}
	:is(.buttons-set) .form__submit {
		width: auto;
	}
}
