.events {
	&__inner {
		display: grid;
		gap: 4rem;
	}
	&__pagination {
		margin-top: 3rem;
		@media screen and (min-width: $screen-ti) {
			margin-top: 5rem;
		}
	}
	#eventsDatePicker {
		display: none;
	}
}
