//        - partner-list,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.partner-list {
	display: grid;
	gap: 3rem;
	@media screen and (min-width: $screen-t) {
		gap: 1.5rem;
	}
	&__item {
		align-items: center;
		color: inherit;
		display: grid;
		gap: 1rem;
		text-decoration: none;
		transition: color .25s ease;
		@media screen and (min-width: $screen-t) {
			gap: 3rem;
			grid-template-columns: 12rem auto;
		}
		&:hover {
			color: var(--front-brand);
		}
	}
	&__logo {
		max-width: 15rem;
	}
	&__image {
		width: 100%;
	}
	&__info {
		display: grid;
	}
	&__title {
		@include style_1\5_1\33(700);
	}
	&__descr {
		color: var(--front-primary_75p);
		@include style_1\5_1\33;
		text-decoration: none;
	}
}
